$.fn.extend({
    animateCss: function (animationName, speedClass) {
    	speedClass = speedClass || "";
        var animationEnd = 'webkitAnimationEnd mozAnimationEnd MSAnimationEnd oanimationend animationend';
        var fullAnimClass = 'animated ' + speedClass + ' ' + animationName;
        this.addClass(fullAnimClass).one(animationEnd, function() {
            $(this).removeClass(fullAnimClass);
        });
    }
});

/* Main */
$(function () {

	function initParticles() {
		window.particlesJS("particles-js", {
		  "particles": {
		    "number": {
		      "value": 80, "density": { "enable": true, "value_area": 800 }
		    },
		    "color": { "value": "#ffffff" },
		    "shape": {
		      "type": "circle",
		      "stroke": { "width": 0, "color": "#000000" },
		      "polygon": { "nb_sides": 5 },
		      "image": { "src": "img/not-used.svg", "width": 100, "height": 100 }
		    },
		    "opacity": {
		      "value": 0.5, "random": false,
		      "anim": { "enable": false, "speed": 1, "opacity_min": 0.1, "sync": false }
		    },
		    "size": {
		      "value": 3, "random": true,
		      "anim": { "enable": false, "speed": 40, "size_min": 0.1, "sync": false }
		    },
		    "line_linked": {
		      "enable": true,
		      "distance": 150, "color": "#ffffff", "opacity": 0.4, "width": 1
		    },
		    "move": {
		      "enable": true, "speed": 2, "direction": "none",
		      "random": false, "straight": false, "out_mode": "out", "bounce": false,
		      "attract": { "enable": false, "rotateX": 600, "rotateY": 1200 }
		    }
		  },
		  "interactivity": {
		    "detect_on": "canvas",
		    "events": {
		      "onhover": { "enable": false, "mode": "bubble" },
		      "onclick": { "enable": true, "mode": "repulse" },
		      "resize": true
		    },
		    "modes": {
		      "grab": { "distance": 140, "line_linked": { "opacity": 1 } },
		      "bubble": {
		        "distance": 400, "size": 4, "duration": 2, "opacity": 8, "speed": 3
		      },
		      "repulse": { "distance": 80, "duration": 0.4 },
		      "push": { "particles_nb": 4 },
		      "remove": { "particles_nb": 2 }
		    }
		  },
		  "retina_detect": true
		});
	}

	// skel.js
	skel.breakpoints({ 
		xlarge: '(max-width: 1800px)',
		large: '(max-width: 1280px)',
		medium: '(max-width: 980px)',
		small: '(max-width: 736px)',
		xsmall: '(max-width: 480px)'
	});

	var $window = $(window);
	var $body = $('body');
	var $header = $('#header');

	// Disable animations/transitions until the page has loaded.
	$body.addClass('is-loading');

	$window.on('load', function() {
		$body.removeClass('is-loading');
	});

	if (skel.vars.mobile) {
		// ----------- MOBILE -------------

		// Turn on touch mode.
		$body.addClass('is-touch');

		// Height fix (mostly for iOS).
		window.setTimeout(function() {
			$window.scrollTop($window.scrollTop() + 1);
		}, 0);

	} else {
		// ----------- NOT MOBILE -------------

		// Load particles
		initParticles();

		// Header - Parallax background
		var parallaxFactor = 90; // lower = more intense
		skel.on('change', function() {
			if (skel.breakpoint('medium').active) {
				$window.off('scroll.parallax_namespace');
				$header.css('background-position', 'top left, center center');
			} else {
				$header.css('background-position', 'left 0px');
				$window.on('scroll.parallax_namespace', function() {
					$header.css('background-position', 'left ' + (-1 * (parseInt($window.scrollTop(), 10) / parallaxFactor)) + 'px');
				});
			}
		});
	}

	$('.scrollup').click(function () {
		/* FadeIn all thumbs */
		var $projectThumbs = $("#projects article");
		$projectThumbs.each(function(index, element) {
			$(element).animateCss("fadeInUp");
		});
		window.scrollTo(0, $("#projects").offset().top);

		/* Enable all */
		$(".project-details").show();
		return false;
	});

	$('.scrolldown').click(function() {
		/* First disable all */
		$(".project-details").hide();

		var $projectSection = $($(this).attr("href"));
		/* Enable only this one */
		$projectSection.show();
		window.scrollTo(0, $projectSection.offset().top);

		$projectSection.animateCss("fadeInUpAbs");
		$projectSection.find("h1").animateCss("pulse");
		return false;
	});

});



